.login {
    margin: auto;
    width: 430px;
    height: auto;
    background-color: #353535;
    border-radius: 5px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.input {
    margin-left: 15px;
    margin-bottom: 13px;
}

.inputText {
    position: relative;
    font-weight: 900;
    margin-bottom: 0;
    color: #BBBBBB;
}

.inputForm {
    width: 400px;
    height: 30px;
    background: #282828;
    color: white;
    border: 0.5px solid #000000;
    box-sizing: border-box;
    border-radius: 3px;
    font-size: 18px;
    font-weight: 600;
}

.inputButton {
    background: #738ADB;
    border-radius: 3px;
    border: 0;
    width: 400px;
    height: 30px;
    margin-left: 15px;
    cursor: pointer;
    color: #FFFFFF;
    font-size: 15px;
    font-weight: 600;
}

.redirect {
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 600;
    margin-left: 15px;
}

.switch {
    padding-left: 5px;
    margin:0;
    color: #738ADB;
}

.bottom{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.errorBorder{
    border: 1px #EB5757 solid;
}